import { type Settings } from "./settings";

const lr = 0.2126;
const lg = 0.7152;
const lb = 0.0722;

export function processImage(
  input: Uint8ClampedArray,
  output: Uint8ClampedArray,
  { temperature, brightness, contrast, vibrance, saturation }: Settings,
) {
  const [tr, tg, tb] = temperatureToRgb(temperature);
  const m = tr * lr + tg * lg + tb * lb;

  const { length } = input;

  for (let i = 0; i < length; i += 4) {
    let r = input[i] / 255;
    let g = input[i + 1] / 255;
    let b = input[i + 2] / 255;

    // Adjust temperature.

    r *= m / tr;
    g *= m / tg;
    b *= m / tb;

    // Adjust brightness.

    r *= brightness;
    g *= brightness;
    b *= brightness;

    // Adjust contrast.

    r += (r - 0.5) * contrast;
    g += (g - 0.5) * contrast;
    b += (b - 0.5) * contrast;

    // Adjust vibrance and saturation.

    const p_max = Math.max(r, g, b);
    const p_min = Math.min(r, g, b);
    const p_lum = (p_max + p_min) / 2;
    const p_sat =
      p_lum < 0.5
        ? (p_max - p_min) / (1e-5 + p_max + p_min)
        : (p_max - p_min) / (1e-5 + Math.max(0, 2 - p_max - p_min));
    const v_bias = 0.8;
    const p_weight =
      (1 - 1.5 * p_sat + (1 + Math.abs(p_lum - 0.5) * 2) * (1 - v_bias)) /
      (1 + (1 - v_bias));
    const s = vibrance * clamp(p_weight, 0, 1) + saturation;
    const l = (r * lr + g * lg + b * lb) / (lr + lg + lb);

    r += (r - l) * s;
    g += (g - l) * s;
    b += (b - l) * s;

    // Output.

    output[i] = r * 255;
    output[i + 1] = g * 255;
    output[i + 2] = b * 255;
    output[i + 3] = input[i + 3];
  }
}

/**
 * @see http://www.tannerhelland.com/4435/convert-temperature-rgb-algorithm-code/
 */
function temperatureToRgb(temperature: number): [number, number, number] {
  temperature /= 100;

  let r: number, g: number, b: number;

  if (temperature <= 66) {
    r = 255;
    g = 99.4708025861 * Math.log(temperature) - 161.1195681661;
    if (temperature <= 19) {
      b = 0;
    } else {
      b = 138.5177312231 * Math.log(temperature - 10) - 305.0447927307;
    }
  } else {
    r = 329.698727446 * Math.pow(temperature - 60, -0.1332047592);
    g = 288.1221695283 * Math.pow(temperature - 60, -0.0755148492);
    b = 255;
  }

  return [clamp(r, 0, 255), clamp(g, 0, 255), clamp(b, 0, 255)];
}

function clamp(value: number, min: number, max: number): number {
  return Math.max(Math.min(value, max), min);
}
