import { type ReactElement, useState } from "react";
import * as styles from "./app.module.css";
import { Doc } from "./doc";
import { Editor } from "./editor";
import { loadImageData } from "./image";
import { ImageSource } from "./image-source";

export function App(): ReactElement {
  const [doc, setDoc] = useState(new Doc(new ImageData(1, 1)));

  const handleSelectImage = (url: string): void => {
    loadImageData(url)
      .then((imageData) => {
        setDoc(new Doc(imageData));
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div className={styles.app}>
      <Editor doc={doc} />
      <ImageSource onSelectImage={handleSelectImage} />
    </div>
  );
}
