import { type ReactElement } from "react";
import * as styles from "./controls.module.css";
import {
  clampSettings,
  initialSettings,
  parameters,
  type Settings,
} from "./settings";

export function Controls({
  settings,
  onChangeSettings,
}: {
  readonly settings: Settings;
  readonly onChangeSettings: (settings: Settings) => void;
}): ReactElement {
  return (
    <div className={styles.controls}>
      {Object.values(parameters).map(({ name, min, max, step }) => (
        <div key={name} className={styles.parameter}>
          <div className={styles.name}>
            {name} ({settings[name]})
          </div>
          <input
            className={styles.input}
            type="range"
            min={min}
            max={max}
            step={step}
            value={settings[name]}
            onInput={(ev) => {
              onChangeSettings(
                clampSettings({
                  ...settings,
                  [name]: (ev.target as HTMLInputElement).valueAsNumber,
                }),
              );
            }}
            onDoubleClick={() => {
              onChangeSettings(
                clampSettings({
                  ...settings,
                  [name]: parameters[name].initial,
                }),
              );
            }}
          />
        </div>
      ))}
      <div
        className={styles.reset}
        onClick={() => {
          onChangeSettings(initialSettings());
        }}
      >
        RESET
      </div>
    </div>
  );
}
